/*--------------------------------------------------------------
# Hero Section
--------------------------------------------------------------*/
#hero {
    width: 100%;
    height: 100vh;
    background: linear-gradient(
      rgba(0, 0, 0, 0), 
      rgba(0, 0, 0, 0.9)
    ),url("./cover.png") top right no-repeat;
    background-size: cover;
    position: relative;
    
  }
  @media (min-width: 992px) {
    #hero {
      padding-left: 160px;
    }
  }
  /* #hero:before {
    content: "";
    background: rgba(255, 255, 255, 0.8);
    position: absolute;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
  } */
  #hero h1 {
    margin: 0;
    font-size: 64px;
    font-weight: 700;
    line-height: 56px;
    color: #ffffff;
    text-shadow:0 0 6px black;
  }
  #hero p {
    color: #ffffff;
    text-shadow:0 0 6px black;
    margin: 15px 0 0 0;
    font-size: 26px;
    font-family: "Poppins", sans-serif;
  }
  #hero p span {
    color: #ffffff;
    text-shadow:0 0 6px black;
    letter-spacing: 1px;
  }
  #hero .social-links {
    margin-top: 30px;
  }
  #hero .social-links a {
    font-size: 24px;
    display: inline-block;
    color: #e1e1eb;
    line-height: 1;
    margin-right: 20px;
    transition: 0.3s;
  }
  #hero .social-links a:hover {
    color: #0563bb;
  }
  @media (max-width: 992px) {
    #hero {
      text-align: center;
    }
    #hero h1 {
      font-size: 32px;
      line-height: 36px;
    }
    #hero p {
      margin-top: 10px;
      font-size: 20px;
      line-height: 24px;
    }
  }